import { Injectable } from "@angular/core";

import { Observable, of } from "rxjs";
import { _HttpClient } from "../net/http.client"
@Injectable({
  providedIn: "root"
})
export class DataService {
  constructor(private _http: _HttpClient) { }

  //æ ¹æ®Id|å¶ä» è·åç¸å³è¡¨ä¿¡æ¯
  getBusinessTables(url: string, params = {}): Observable<any> {
    return this._http.get(url, params);
  }

  getFormInfo(url: string, params = {}): Observable<any> {
    return this._http.get(url, params);
  }

  //ç³»ç»æ§ä»¶-äººå
  getSysCtrEmployee(params = {}): Observable<any> {
    //let url = "emptable.json";
    let url = this.getWebAPIName("employee");
    let result = this._http.get(url, params);
    return result;
  }

  //ç³»ç»æ§ä»¶-ç»ç»
  getSysCtrOrgTree(params = {}): Observable<any> {
    //let url = "org-tree.json";
    let url = this.getWebAPIName("organization");
    return this._http.get(url, params);
  } 

  //ç³»ç»æ§ä»¶-èä½
  getSysCtrPostionTable(params = {}): Observable<any> {
    //let url = "postiontable.json";
    let url = this.getWebAPIName("postion");
    return this._http.get(url, params);
  }

  //ç³»ç»æ§ä»¶-å°åº
  getSysCtrArea(params = {}): Observable<any> {
    let url = "tables.json";

    return this._http.get(url, params);
  }

  //æ å½¢æ°æ®
  getTreeData(params = {}): Observable<any> {
    //let url = "tree.json";
    let url = this.getWebAPIName("tree");
    return this._http.get(url, params);
  } 

  getWebAPIName(type: string) {
    let result = "";
    let res = localStorage.getItem("VXFORM:WEBAPI");
    if (res != undefined && res != null && res != "") {
      let webapi = JSON.parse(res);
      result = webapi[type];
    }
    if (result == undefined || result == null) {
      result = "";
    }
    return result;
  }
}
