import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  OnChanges,
  SimpleChanges
} from "@angular/core";
import { VXFormFieldConfig } from "@vx-form/core";
import * as _ from "lodash";

@Component({
  selector: "vxsurvey-editcascader",
  templateUrl: "./index.component.html",
  styleUrls: ["./index.component.less"]
})
export class VXSurveyEditCascader implements OnInit, OnChanges {
  
  cascaderData: any[] = [];

  @Input() set fieldConfig(value:VXFormFieldConfig){
    if(value!=undefined && value!=null && value.templateOptions !=undefined && value.templateOptions!=null && value.templateOptions.options!=undefined && value.templateOptions.options!=null){
      if(Array.isArray(value.templateOptions.options)){
        this.cascaderData = _.cloneDeep(value.templateOptions.options);
      }else{
        this.cascaderData = []; 
      }
    }
    this.row1.data = this.cascaderData;
  };

  @Input() set data(value: any[]) {
    
  }

  get data() {
    return this.cascaderData;
  }

  row1: any = {
    cascader: 1,
    current: "",
    showadd: true,
    data: []
  }
  row2: any = {
    cascader: 2,
    current: "",
    showadd: false,
    data: []
  }
  row3: any = {
    cascader: 3,
    current: "",
    showadd: false,
    data: []
  }
  row4: any = {
    cascader: 4,
    current: "",
    showadd: false,
    data: []
  }



  constructor() {

  }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges) {
  } 

  cascaderItem(item: any, type: number) {
    let showadd = true; 
    switch (type) {
      case 1:
        this.row1.current = item.value;
        this.row2.cascader = 2;
        this.row2.showadd = showadd;
        this.row2.data = item.children ? item.children : [];

        this.row3.current = "";
        this.row3.showadd = false;
        this.row3.data = [];

        this.row4.current = "";
        this.row4.showadd = false;
        this.row4.data = [];
        break;
      case 2:
        this.row2.current = item.value;
        this.row3.cascader = 3;
        this.row3.showadd = showadd;
        this.row3.data = item.children ? item.children : [];

        this.row4.current = "";
        this.row4.showadd = false;
        this.row4.data = [];
        break;
      case 3:
        this.row3.current = item.value;
        this.row4.cascader = 4;
        this.row4.showadd = showadd;
        this.row4.data = item.children ? item.children : [];
        break;
      case 4:
        this.row4.current = item.value;
        break;
    }
  }

  addOption(e: number) {

  }

  delOption(e: any, type: number) {
    if (e) {
      switch (type) {
        case 1:
          this.row1.data.forEach((item: any, index: number, obj: any) => {
            if (e.value == item.value) {
              obj.splice(index, 1);
            }
          });
          this.row2 = [];
          this.row3 = [];
          this.row4 = [];
          break;
        case 2:
          this.row2.data.forEach((item: any, index: number, obj: any) => {
            if (e.value == item.value) {
              obj.splice(index, 1);
            }
          });
          this.row3 = [];
          this.row4 = [];
          break;
        case 3:
          this.row3.data.forEach((item: any, index: number, obj: any) => {
            if (e.value == item.value) {
              obj.splice(index, 1);
            }
          });
          this.row4 = [];
          break;
        case 4:
          this.row4.data.forEach((item: any, index: number, obj: any) => {
            if (e.value == item.value) {
              obj.splice(index, 1);
            }
          });
          break;
      }

    }
  }
}