import {
  Component,
  OnInit,
  OnChanges,
  OnDestroy,
  Input,
  Output,
  EventEmitter,
  SimpleChanges
} from "@angular/core";
import { FormGroup } from "@angular/forms";
import {
  VXFormFieldConfig,
  VXTableFieldConfig,
  VXSurveyInfo,
  VXSurveyPage,
  clone,
  VXTableConfig,
  VXFormCommonService
} from "@vx-form/core";
import { NzModalService } from "ng-zorro-antd/modal";
import { NzMessageService } from "ng-zorro-antd/message";
import * as _ from "lodash";
import { VXFormAntService } from "../../tools/service";
import {
  FormLayout,
  LayoutPanel,
  LayoutColumn,
  VXTableBusiness,
  ICheckedField,
  IBusinessTable,
  Itable,
  IDesignFormAPI
} from "../../tools/util";
import {
  CdkDragDrop,
  moveItemInArray,
  transferArrayItem
} from "@angular/cdk/drag-drop";
import { VXDialogService } from "../../service/dialog.service";
import { DataService } from "../../service/data.service";
import { VXTransferService } from "../../tools/transferService";
import { group } from "@angular/animations";
import { filter } from "rxjs/operators";
import { Guid } from "@vx-form/core";
import { NzTreeHigherOrderServiceToken } from "ng-zorro-antd";

@Component({
  selector: "vxsurvey-design-detail",
  templateUrl: "./detail.component.html",
  styleUrls: ["./detail.component.less"],
  providers: [DataService, VXTransferService]
})
export class VXSurveyDesignDetail implements OnInit, OnChanges, OnDestroy {
  @Input() formCode: string = "";
  @Input() api: IDesignFormAPI = {
    form: { url: "assets/mockData/survey.json" }
  };
  @Output() saveEvent: EventEmitter<string> = new EventEmitter<string>();
  @Output() cancelEvent: EventEmitter<string> = new EventEmitter<string>();
  @Output() previewEvent: EventEmitter<string> = new EventEmitter<string>();

  pageStatus: number = 1;

  form_model: any = {};
  form_group = new FormGroup({});
  form_fields: VXFormFieldConfig[] = [];

  vxSurveyInfo: VXSurveyInfo = {
    code: "",
    name: "",
    first: {
      title: "",
      text: ""
    },
    last: {
      title: "",
      text: ""
    },
    pages: [
      {
        code: "page1",
        name: "ç¬¬1é¡µ",
        questions: []
      }
    ]
  };
  show_setting = false;
  questionField: VXFormFieldConfig = {};
  pageSelectedIndex: number = 0;
  constructor(
    private message: NzMessageService,
    private modalService: NzModalService,
    private vxFormAntService: VXFormAntService,
    public vxDialogService: VXDialogService,
    public dataService: DataService,
    public vxTransferService: VXTransferService,
    public vxFormCommonService: VXFormCommonService
  ) { }


  ngOnInit() {
    this.vxFormAntService.questionEvent.subscribe((res: any) => {
      this.questionOperational(res);
    });

    this.vxFormAntService.optionEvent.subscribe((res: any) => {
      this.optionOperational(res);
    });

    this.vxFormAntService.dragDropEvent.subscribe((res: any) => {
      this.addQuestionDragDrop(res);
    });

  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.formCode) {
      this.initFormInfo();
    }
  }

  ngOnDestroy() { }

  /*æé®åºå---begin*/

  saveSurvey() {
    let objSurvey = clone(this.vxSurveyInfo);
    objSurvey.pages.forEach((item: any) => {
      this.vxFormCommonService.initFormFieldProperty(item.questions);
    })
    this.saveEvent.emit(JSON.stringify(objSurvey));
  }


  initFormInfo() {
    this.dataService.getFormInfo(this.api.form.url, { formcode: this.formCode }).subscribe(res => {
      this.vxSurveyInfo = {
        code: res.code,
        name: res.name,
        type: res.type,
        first: res.first,
        last: res.last,
        pages: res.pages
      };
      if (this.vxSurveyInfo.pages != undefined && (this.vxSurveyInfo.pages == null || this.vxSurveyInfo.pages.length == 0)) {
        this.vxSurveyInfo.pages.push({
          "code": "pageCode1",
          "name": "ç¬¬1é¡µ",
          "questions": []
        })
      }
      this.getQuestionListByPageIndex(0);
    });
  }

  pageClick(e: number) {
    // if (this.vxSurveyInfo.pages != undefined) {
    //   if (e == 0 || this.vxSurveyInfo.pages.length == e - 1) {
    //     this.show_setting = false;
    //   }
    //   else {
    //     if (e > 0) {
    //       this.getQuestionListByPageIndex(e - 1);
    //     }
    //   }
    // }
    this.getQuestionListByPageIndex(e);
  }

  addPage() {
    if (this.vxSurveyInfo.pages != undefined) {
      let newPage = {
        code: `pagecode_${this.vxSurveyInfo.pages.length + 1}`,
        name: `ç¬¬${this.vxSurveyInfo.pages.length + 1}é¡µ`,
        questions: []
      }
      this.vxSurveyInfo.pages.push(newPage);
    }
  }

  deletePage(index: number) {
    if (this.vxSurveyInfo.pages != undefined) {
      this.vxSurveyInfo.pages.splice(index, 1);
    }
  }

  getQuestionListByPageIndex(pageIndex: number) {
    if (this.vxSurveyInfo.pages != undefined && this.vxSurveyInfo.pages.length > 0 && this.vxSurveyInfo.pages[pageIndex].questions != undefined) {
      this.form_fields = [{
        "type": "drag-drop",
        "key": "drag-droplist",
        "fieldGroup": this.vxSurveyInfo.pages[pageIndex].questions
      }]
    }
  }



  //é¢ç®
  questionOperational(res: any) {
    let resType: any = res["sign"];
    let resField: any = res["field"];
    let rQuestion: any;
    let rIndex: any;
    this.formFieldGroup.forEach((item: any, index: any, object: any) => {
      item.active = false;
      if (item.key == resField.key) {
        item.active = true;
        rQuestion = item;
        rIndex = index;
      }
    });

    switch (resType) {
      case "active":
        this.questionField = rQuestion;
        this.show_setting = true;
        break;
      case "logic":
        this.message.success("é»è¾è®¾ç½®")
        break;
      case "copy":
        let copyQuestion = _.cloneDeep(rQuestion);
        copyQuestion.key = Guid.create()["value"];
        copyQuestion.active = false;
        this.formFieldGroup.splice(rIndex + 1, 0, copyQuestion);
        this.message.success("æ·è´é¢ç®")
        break;
      case "delete":
        this.formFieldGroup.splice(rIndex, 1)
        this.message.success("å é¤é¢ç®");
        this.questionField = {};
        this.show_setting = false;
        break;
    }
  }

  //é¢ç®éé¡¹
  optionOperational(res: any) {
    let resType: any = res["sign"];
    let resOption: any = res["option"];
    let resField: any = res["field"];
    let rQuestion: any;
    let rIndex: any;
    this.formFieldGroup.forEach((item: any, index: any, object: any) => {
      item.active = false;
      rIndex = index;
      if (item.key == resField.key) {
        item.active = true;
        rQuestion = item;
      }
    });
    switch (resType) {
      case "add":
        resField.templateOptions.options.push({
          "label": "éé¡¹",
          "value": Guid.create()["value"],
          "score": 0,
          "input": false
        });
        this.message.success("æ·»å éé¡¹")
        break;
      case "adds":
        this.message.success("æ¹éæ·»å ")
        break;
      case "delete":
        resField.templateOptions.options.forEach((item: any, index: any, object: any) => {
          if (item.value == resOption.value) {
            object.splice(index, 1);
          }
        });
        this.message.success("å é¤éé¡¹")
        break;

      case "subtitle":
        resField.templateOptions.subtitle.push({
          "label": "ç©éµè¡",
          "value": Guid.create()["value"]
        });
        this.message.success("æ·»å ç©éµè¡")
        break;
    }
  }

  //æ·»å é¢ç®
  addQuestionType(data: any, index: any = -1) {
    this.vxTransferService.transferQuestionByType(data, this.formFieldGroup, index);
  }

  //ææ½æ·»å é¢ç®
  addQuestionDragDrop(res: any) {
    let data = res["previousData"][res["previousIndex"]];
    this.vxTransferService.transferQuestionByType(data, this.formFieldGroup, res["currentIndex"])
  }

  get formFieldGroup() {
    let result: any[] = [];
    if (this.form_fields != null && this.form_fields.length > 0 && this.form_fields[0].fieldGroup != undefined) {
      result = this.form_fields[0].fieldGroup;
    }
    return result;
  }
}
