import {
  Component,
  ViewChild,
  EventEmitter,
  ViewChildren,
  Input,
  Output,
  forwardRef,
} from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

import { ContenteditableModel } from './contenteditable'; 

const EDITOR_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => InlineEditorComponent),
  multi: true,
};

// <div
//       [(contenteditableModel)]="value"
//       [ngClass]="active==true?'inline_editor':'inline_editor_hidden'"
//       role="textbox"
//       [attr.contenteditable]="disabled ? 'false' : 'true'"
//       [class.disabled]="disabled"
//       (blur)="handleBlur($event)"
//       (focus)="handleFocus($event)"
//       [brMode]="brMode"
//       [placeholder]="placeholder"
//       [debounce]="debounce"
//       (contenteditableModelChange)="onContenteditableModelChange($event)"
//     ></div>

@Component({
  selector: 'inline-editor',
  providers: [EDITOR_VALUE_ACCESSOR],
  template: `
    <input *ngIf="active==true"  nz-input [(ngModel)]="value" /> 
    <div *ngIf="active==false" 
      class="inline_editor_show"
      [innerHTML]="value | safeHtml">
    </div>
  `,
  styles: [
    `
      :host ::ng-deep {
      }
    `,
  ],
})
export class InlineEditorComponent implements ControlValueAccessor {
  _value="";
  _classList = {};
  @Input() debounce: string = '100';
  @Input() placeholder: string = '';
  @Input() className: string = 'inline_editor';
  @Input() brMode = true;
  @Output() blur = new EventEmitter();
  @Output() focus = new EventEmitter();
  @Input() disabled = false;
  @Input() data:any="";
  @Input() active:boolean=false 
  //@ViewChild(ContenteditableModel,{static:true}) contenteditableModel: ContenteditableModel|undefined;

  constructor() {}

  //   focus() {
  //     this.contenteditableModel.doFocus();
  //   }

  ngOnInit() {
    // if (this.className) {
    //   var splits = this.className.split(' ');
    //   for (var i = 0; i < splits.length; i++) {
    //     this._classList[splits[i]] = true;
    //   }
    // }
  }

  handleFocus(evt:any) {
    this.focus.emit(evt);
  }

  handleBlur(evt:any) {
    this.blur.emit(evt);
  }

  get value(): any {
    return this._value;
  }
  @Input()
  set value(v:any) {
    if (v !== this._value) {
      this._value = v;
      this.onChange(v);
    }
  } 
  
  onContenteditableModelChange(evt:any) {
    this.onTouched();
    this.onChange(evt);
  }

  /**
   * Implements ControlValueAccessor
   */
  writeValue(value: any) {
    this._value = value;
    //if (this.contenteditableModel && this.contenteditableModel.instance) {
      //this.contenteditableModel.instance.setData(value);
    //}
  }
  onChange(_: any) {}
  onTouched() {}
  registerOnChange(fn: any) {
    this.onChange = fn;
  }
  registerOnTouched(fn: any) {
    this.onTouched = fn;
  }
}
