  create_matrix(questionType: any) {
    let question: VXFormFieldConfig = {
      status: 1,
      key: Guid.create()["value"],
      type: questionType.type
    }
    switch (questionType.type) {
      case "matrix_radio":
        this.createMatrix_radio(question);
        break;
      case "matrix_checkbox":
        this.createMatrix_radio(question);
        break;
      case "matrix_score":
        this.createMatrix_score(question);
        break;
      case "matrix_input":
        this.createMatrix_input(question);
        break;
      default:
        question.type = "matrix_radio";
        this.createMatrix_radio(question);
        break;
    } 
    return question;
  }

