import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  OnChanges,
  SimpleChanges
} from "@angular/core";
import { VXFormFieldConfig, VXSurveyInfo } from "@vx-form/core";
import * as _ from "lodash";
import { CdkDragDrop, moveItemInArray, transferArrayItem } from '@angular/cdk/drag-drop';
import { DataService } from "../../service/data.service";

import { ITreeData, ICheckedTree } from "../../tools/util";

@Component({
  selector: "vx-question-list",
  templateUrl: "./index.component.html",
  styleUrls: ["./index.component.less"]
})
export class VXQuestionList implements OnInit, OnChanges {
  @Input() vxSurveyInfo: VXSurveyInfo = {};
  @Output() questionTypeEvent = new EventEmitter<any>();
  @Output() addPageEvent = new EventEmitter<any>();



  constructor(public dataService: DataService) { }

  ngOnInit() {

  }

  ngOnChanges(changes: SimpleChanges) {

  }

  dropQuestion(event: CdkDragDrop<string[]>) { 
    if (_.get(event, "container.data.questions")) {
      if (event.previousContainer === event.container) {
        moveItemInArray(_.get(event, "container.data.questions"), event.previousIndex, event.currentIndex);
      } else {
        if (_.get(event, "previousContainer.data.questions")) {
          transferArrayItem( 
            _.get(event, "previousContainer.data.questions"),
            _.get(event, "container.data.questions"),
            event.previousIndex,
            event.currentIndex);
        }
      }
    } 
  }
}
