import { Injectable, EventEmitter } from "@angular/core";
import { VXFormFieldConfig } from "@vx-form/core";

@Injectable()
export class VXFormAntService {
  btnEvent = new EventEmitter();
  fieldEvent = new EventEmitter();
  questionEvent = new EventEmitter();
  optionEvent = new EventEmitter(); 
  dragDropEvent = new EventEmitter();

  workList = [];

  constructor() {}

  fieldOperational(sign: string, field: VXFormFieldConfig) {
    this.fieldEvent.emit({
      sign: sign,
      field: field
    });
  }
  groupOperational(type: string, field: VXFormFieldConfig) {
    this.btnEvent.emit({
      type: type,
      field: field
    });
  }
  

  
  questionOperational(sign: string, field: VXFormFieldConfig) {
    this.questionEvent.emit({
      sign: sign,
      field: field
    });
  }
  
  optionOperational(sign: string,option:any, field: VXFormFieldConfig) {
    this.optionEvent.emit({
      sign: sign,
      option:option,
      field: field
    });
  }

  dragDropOperational(previousData:any,previousIndex:any,currentIndex:any) {
    this.dragDropEvent.emit({
      previousData: previousData,
      previousIndex:previousIndex,
      currentIndex: currentIndex
    });
  }
  

}
