import { Injectable } from "@angular/core";
import { NzModalService, UploadFile } from "ng-zorro-antd";

type ModalSizeType = "small" | "medium" | "large";
export interface ModalOptions {
  nzContent: any; //ç»ä»¶
  size: ModalSizeType; //å°/ä¸­/å¤§
  nzTitle?: string; //æ é¢
  primaryLabel?: string; //æé®âlabelâ,ä¸ä¼ é»è®¤ä¸ºâç¡®å®â
  vertical?: boolean; //å¼¹æ¡æ¯å¦ä¸ºåç´å±ä¸­ï¼é»è®¤ä¸åç´å±ä¸­
  hideBtn?: boolean; //æ¯å¦éèfooteræé®
  showResetBtn?: boolean; //æ¯å¦æ¾ç¤ºéç½®æé®
}
@Injectable({
  providedIn: "root"
})
export class VXDialogService {
  constructor(public modalService: NzModalService) {}
  switchSize(size: any) {
    const obj = {
      width: 700,
      height: 500
    };
    switch (size) {
      case "small":
        obj.width = 530;
        obj.height = 400;
        break;
      case "medium":
        obj.width = 700;
        obj.height = 500;
        break;
      case "large":
        obj.width = 950;
        obj.height = 600;
        break;
      default:
        obj.width = 700;
        obj.height = 500;
        break;
    }
    return obj;
  }
  public CreateModelDialog(
    modalOptions: ModalOptions,
    params: any,
    callback: any
  ) {
    const width = this.switchSize(modalOptions.size).width;
    const height = this.switchSize(modalOptions.size).height;
    const modal: any = this.modalService.create({
      nzTitle: modalOptions.nzTitle ? modalOptions.nzTitle : "æç¤º",
      nzWidth: width + "px",
      nzClosable: true,
      nzMaskClosable: false,
      nzWrapClassName: modalOptions["vertical"] ? "vertical-center-modal" : "",
      nzBodyStyle: {
        padding: "0",
        height: !modalOptions["hideBtn"]
          ? height - 107 + "px"
          : height - 54 + "px"
      },
      nzContent: modalOptions.nzContent,
      nzFooter: !modalOptions["hideBtn"]
        ? [
            {
              show: true,
              label: "åæ¶",
              type: "default",
              onClick: componentInstance => {
                modal.destroy();
              }
            },
            {
              show: modalOptions["showResetBtn"],
              label: "éç½®",
              type: "default",
              onClick: componentInstance => callback(componentInstance, modal)
            },
            {
              show: true,
              label: modalOptions["primaryLabel"]
                ? modalOptions["primaryLabel"]
                : "ç¡®å®",
              type: "primary",
              onClick: componentInstance => callback(componentInstance, modal)
            }
          ]
        : null,
      nzOnOk: data => {},
      nzOnCancel: data => {},
      nzComponentParams: params
    });

    modal.afterClose.subscribe((result: any) => {
      if (result) {
        callback(result);
      }
    });
  }
}
